.nav {
    background-color: #FFC2D1;
    height: 6em;
    display: flex;
    width: 100%;
}

.navstick {
    background-color: #FFC2D1;
    height: 6em;
    display: flex;
    position: fixed;
    top: 0px;
    width: 100%;
    z-index: 4;
}

.container a:link {
    text-decoration: none;
}

.navFirst {
    width: 30%;
    text-align: center;
    padding-top: 1%;
}

.anna {
    color: black;
    font-family: 'Tangerine';
    font-size: 80px;
}

.navSecond {
    width: 70%;
    padding-top: 1.5%;
    display: flex;
    font-family: 'Zen Kaku Gothic New';
    font-size: 40px;
    text-align: center;
}

.link1 {
    color: black;
    width: 25%;
}

.link2 {
    color: black;
    width: 35%;
}

.link3 {
    color: black;
    width: 40%;
}

@media (max-width: 1015px){
    .anna{
        font-size: 70px;
    }

    .navFirst{
        padding-top: 2%;
        width: 40%;
    }

    .navSecond {
        padding-top: 3%;
        width: 60%;
        font-size: 30px;
    }
}

@media (max-width: 670px){
    .anna{
        font-size: 55px;
    }

    .navFirst{
        padding-top: 4%;
    }

    .navSecond {
        padding-top: 4.5%;
        font-size: 25px;
    }
}

@media (max-width: 670px){
    .anna{
        font-size: 45px;
    }

    .navFirst{
        padding-top: 1%;
    }

    .navSecond {
        padding-top: 1.5%;
        font-size: 20px;
    }

    .nav {
        height: 3em;
    }
    
    .navstick {
        height: 3em;
    }
}

@media (max-width: 450px){
    .anna{
        font-size: 35px;
    }

    .navFirst{
        padding-top: 2%;
    }

    .navSecond {
        padding-top: 2.5%;
        font-size: 17px;
    }

    .nav {
        height: 3em;
    }
    
    .navstick {
        height: 3em;
    }
}