.nav {
    z-index: 4;
}

.container {
    display: flex;
    background-color: #FFE5EC;
    height: 550px;
    width: 100%;
    margin-bottom: 10%;
}

.containertwo {
    display: flex;
    background-color: #FFE5EC;
    height: 100%;
    width: 100%;
    margin-bottom: 10%;
}

.containerthree {
    display: flex;
    background-color: #FFE5EC;
    height: 100%;
    width: 100%;
    margin-bottom: 3%;
}

.left {
    width: 60%;
    height: 100%;
    text-align: center;
    padding-top: 6%;
}

.lefttwo {
    width: 50%;
    padding-left: 5%;    
}

.leftthree {
    width: 50%;
    padding-left: 5%;    
    padding-top: 5%;
}

.right {
    width: 45%;
    height: 100%;
    justify-content: left;
    display: flex;
}

.anna {
    height: 550px;
}

.left h1 {
    margin-left: 30%;
    width: 60%;
    font-size: 40px;
    border-bottom: 1px solid black;
    font-weight: normal;
}

.left h2 {
    font-size: 30px;
    margin-left: 25%;
    width: 68%;
    text-align: center;
    font-weight: normal;
}

.lefttwo {
    width: 50%;
}

.righttwo {
    width: 50%;
}

.lefttwo h1 {
    width: 100%;
    font-size: 40px;
    font-weight: normal;
    text-align: center;
    text-decoration: underline 1px;
    text-underline-offset: 10px;
}

.lefttwo h2 {
    width: 100%;
    font-size: 30px;
    text-align: center;
    font-weight: normal;
}

.lefttwo h3 {
    width: 100%;
    font-size: 30px;
    text-align: center;
    color: black;
    text-decoration: none;
    font-weight: bold;
}

.leftthree h1 {
    width: 100%;
    font-size: 40px;
    font-weight: normal;
    text-align: center;
    text-decoration: underline 1px;
    text-underline-offset: 10px;
}

.leftthree h2 {
    width: 100%;
    font-size: 30px;
    text-align: center;
    font-weight: normal;
}

.containertwo a:link {
    text-decoration: none;
}

.righttwo {
    display: flex;
    align-items: center;
    justify-content: center;
}

.atendone {
    width: 35%;
    height: 65%;
    border-radius: 30px;
    transition: 0.2s;
    z-index: 1;
}

.atendone:hover {
    width: 40%;
    height: 75%;
}

.atendtwo {
    width: 25%;
    height: 55%;
    border-radius: 30px;
    transform: rotate(20deg);
    margin-left: -10%;
    filter: blur(0.5px) grayscale(50%);
    transition: 0.2s;
}

.atendtwo:hover {
    width: 40%;
    height: 75%;
    transform: rotate(0deg);
    filter: blur(0px) grayscale(0%);
}

.centerImgToLeft {
    width: 30%;
    height: 60%;
    border-radius: 30px;
    transform: rotate(-10deg);
    filter: blur(0.5px) grayscale(50%);
    transition: 0.2s
}

.centerImgToRight {
    width: 30%;
    height: 60%;
    border-radius: 30px;
    z-index: 1;
    transform: rotate(10deg);
    filter: blur(0.5px) grayscale(50%);
    transition: 0.2s
}

.atendthree {
    width: 25%;
    height: 55%;
    border-radius: 30px;
    transform: rotate(-20deg);
    filter: blur(0.5px) grayscale(50%);
    margin-right: -10%;
    transition: 0.2s;
}

.atendthree:hover {
    width: 40%;
    height: 75%;
    transform: rotate(0deg);
    filter: blur(0px) grayscale(0%);
    z-index: 2;
}

.ciosp {
    height: 80%;
}

.link {
    color: black;
    font-weight: bold;
}

