.clinica {
    background-color: #FFE5EC;
}

.clinica h1 {
    margin-top: 0;
    padding-top: 3%;
    font-size: 40px;
    width: 100%;
    text-align: center;
    font-weight: normal;
    text-decoration: underline;
}

.clinicadiv {
    width: 100%;
    justify-content: center;
    display: flex;
    margin-top: -3%;
    margin-bottom: 5%;
}

.clinicadiv h2 {
    font-size: 30px;
    width: 50%;
    text-align: center;
    font-weight: normal;
    padding-bottom: 3%;
}

@media (max-width: 1015px) {
    .clinicadiv h2 {
        width: 60%;
    }
}

@media (max-width: 1015px) {
    .clinicadiv h2 {
        width: 60%;
        font-size: 25px;
    }

    .clinica h1 {
        font-size: 35px;
    }
    
}

@media (max-width: 700px) {
    .clinicadiv h2 {
        width: 60%;
        font-size: 20px;
    }

    .clinica h1 {
        font-size: 30px;
    }
    
}

@media (max-width: 600px) {
    .clinicadiv h2 {
        width: 70%;
        font-size: 15px;
    }

    .clinica h1 {
        font-size: 25px;
    }
    
}

@media (max-width: 415px) {
    .clinicadiv h2 {
        width: 70%;
        font-size: 15px;
    }

    .clinica h1 {
        font-size: 20px;
    }
    
}