.casosdiv {
    background-color: #FFE5EC;
    text-align: center;
    padding-bottom: 5%;
    margin-bottom: 2%;
}

.casosdiv h1 {
    font-weight: normal;
    font-size: 40px;
    padding-top: 1%;
}

.containerbotoes {
    width: 30%;
    display: flex;
    margin-left: 15%;
    margin-bottom: -4%;
    margin-top: -2%;
}

.containerbotoes h1 {
    font-weight: normal;
    font-size: 24px;
    border: 0;
    padding: 0;
    margin: 0.5%;
}

.botaoantes {
    background-color: #FFD4E0;
    width: 30%;
    height: 10px;
    padding-bottom: 20%;
    border-radius: 50%;
    margin-right: -5%;
    position: relative;
    z-index: 1;
    cursor: pointer;
}

.botaoantesclicked {
    background-color: #FFD4E0;
    width: 30%;
    height: 10px;
    padding-bottom: 20%;
    border-radius: 50%;
    margin-right: -5%;
    position: relative;
    z-index: 2;
    cursor: pointer;
}

.botaodepois {
    background-color: #F9BFCF;
    width: 30%;
    height: 10px;
    padding-bottom: 20%;
    border-radius: 50%;
    position: relative;
    z-index: 1;
    cursor: pointer;
}

.botaodepoisclicked {
    background-color: #F9BFCF;
    width: 30%;
    height: 10px;
    padding-bottom: 20%;
    border-radius: 50%;
    position: relative;
    z-index: 2;
    cursor: pointer;
}

.casecontainerantes {
    background-color: #FFD4E0;
    margin-left: 5%;
    margin-right: 5%;
    width: 90%;
    display: flex;
    border-radius: 500px;
    position: relative;
    z-index: 3;
}

.casecontainerdepois {
    background-color: #F9BFCF;
    margin-left: 5%;
    margin-right: 5%;
    width: 90%;
    display: flex;
    border-radius: 500px;
    position: relative;
    z-index: 3;
}

.imagecontainer {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.antes {
    width: 70%;
    padding: 5%;
    border-radius: 110px;
}

.depois {
    width: 70%;
    padding: 5%;
    border-radius: 110px;
}

.textcontainer {
    width: 50%;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.textcontainer h1 {
    font-weight: normal;
    font-size: 25px;
    padding: 10%;
}

@media (max-width: 1020px){
    .containerbotoes h1 {
        font-size: 18px;
        border: 0;
        padding: 5%;
        padding-left: 12%;
        margin: 0.5%;
    }
    
    .casecontainerdepois {
        border-radius: 100px;
    }

    .casecontainerantes {
        border-radius: 100px;
    }

    .textcontainer h1 {
        font-weight: normal;
        font-size: 20px;
        padding: 10%;
    }    

    .imagecontainer {
        padding-left: 5%;
    }

    .textcontainer {
        padding-right: 5%;
    }
}

@media (max-width: 800px){
    .casosdiv h1 {
        font-size: 30px;
    }

    .containerbotoes h1 {
        font-size: 15px;
        border: 0;
        padding: 5%;
        padding-left: 12%;
        margin: 0.5%;
    }
    
    .casecontainerdepois {
        border-radius: 100px;
    }

    .casecontainerantes {
        border-radius: 100px;
    }

    .textcontainer h1 {
        font-weight: normal;
        font-size: 20px;
        padding: 10%;
    }    

    .antes {
        width: 90%;
        height: 70%;
        padding: 5%;
        border-radius: 100px;
    }
    
    .depois {
        width: 90%;
        height: 70%;
        padding: 5%;
        border-radius: 100px;
    }
}

